<template lang="html">
  <div class="">
    <input class="w3-input opacity w3-text-white" type="text" name="" v-model="recclass" @input="onChange" @keydown.down="onArrowDown" @keydown.up="onArrowUp" @keydown.enter="onEnter" :class="{'w3-border-red' : !classUse}" :placeholder="PlaceholderUse">
    <ul v-show="isOpen" class="w3-ul">
      <li v-for="(result, index) in results" :key="index" @click="setResult(result)" :class="{ 'w3-blue': index === arrowCounter }" @mouseover="onMouseOver(index)">{{result}}</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Autocomplete",
  props: {
    classUse : {
      type: Boolean,
      required: false,
      default: true
    },
    PlaceholderUse : {
      type: String,
      required: false,
      default: ''
    }
  },
  data() {
    return {
      recclass : '',
      items : ["E3", "EH3", "EL3", "E4", "EH4", "EL4", "E5", "EH5", "EL5", "E6", "EH6", "EL6", "E7", "EH7", "EL7", "H3", "H3.1", "H3.2", "H3.3", "H3.4", "H3.5", "H3.6", "H3.7", "H3.8", "H3.9", "H4", "H5", "H6", "H7", "L3", "L3.1" , "L3.2", "L3.3", "L3.4", "L3.5", "L3.6", "L3.7", "L3.8", "L3.9", "L4", "L5", "L6", "L7", "LL3", "LL3.1" , "LL3.2", "LL3.3", "LL3.4", "LL3.5", "LL3.6", "LL3.7", "LL3.8", "LL3.9", "LL4", "LL5", "LL6", "LL7", "CL", "CM1", "CM1.1", "CM1.2", "CM1.3", "CM1.4", "CM1.5", "CM1.6", "CM1.7", "CM1.8", "CM1.9", "CM2", "CV2", "CV2.1", "CV2.2", "CV2.3", "CV2.4", "CV2.5", "CV2.6", "CV2.7", "CV2.8", "CV2.9", "CV3", "CV3.1", "CV3.2", "CV3.3", "CR", "CO3", "CO3.1", "CO3.2", "CO3.3", "CO3.4", "CO3.5", "CO3.6", "CO3.7", "CK", "CB", "CH", "K", "R"],
      results : [],
      isOpen: false,
      arrowCounter: -1
    }
  },
  methods: {
    onChange () {
      this.isOpen = true;
      this.filterResults();
    },
    filterResults() {
      this.results = this.items.filter(item => item.toLowerCase().indexOf(this.recclass.toLowerCase()) > -1 );
    },
    setResult(input) {
      this.recclass = input;
      this.isOpen = false;
    },
    onMouseOver(input) {
      this.arrowCounter = input;
    },
    onArrowDown() {
      if (this.arrowCounter < this.results.length) {
          this.arrowCounter = this.arrowCounter + 1;
      }
    },
    onArrowUp() {
      if (this.arrowCounter > 0) {
          this.arrowCounter = this.arrowCounter - 1;
      }
    },
    onEnter() {
      this.recclass = this.results[this.arrowCounter];
      this.isOpen = false;
      this.arrowCounter = -1;
    },
  },
  watch: {
    results() {
      if(this.recclass.length === 0) {
        this.isOpen = false;
      }
    },
    recclass() {
      this.$emit('meteorClass', this.recclass);
    }
  }
}
</script>

<style lang="css">
  .opacity {
    background-color: rgba(0, 0, 0, 0.5);
  }
</style>
